import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Calculation Game';
  start = false;
  maxLevel = 1;
  level = 1;
  a = 0;
  b = 0;
  operator = '+';
  abCal = 0;
  result = '';
  decimal = false;
  point = 0;
  gameTime = 15;
  countDownTime = this.gameTime;
  plusTime = Math.ceil(this.countDownTime / 12) + 2;
  interval: any;

  constructor() {

  }

  startGame() {
    if (this.start === false) {
      this.start = true;
    }

    this.level = 1;
    this.a = 0;
    this.b = 0;
    this.point = 0;
    this.countDownTime = this.gameTime;
    this.clearResult();
    this.startCountdown();
    this.calculation();
  }

  calculation() {
    if (this.randomNumber(2) > 0) {
      this.plus();
      this.operator = '+';
    } else {
      this.operator = '-';
      this.divi();
    }
    if (this.countDownTime > 30) {
      this.countDownTime = 30;
    }
  }

  plus() {
    const critLevel = (this.level + 1) * 2;
    this.a = this.randomNumber(critLevel);
    this.b = this.randomNumber(critLevel);
    if (this.a < this.b) {
      const temp = this.a;
      this.a = this.b;
      this.b = temp;
    }
    this.abCal = this.a + this.b;
    if (this.a === 0 || this.b === 0 || this.abCal < critLevel) {
      this.plus();
    }
  }

  divi() {
    const critLevel = (this.level + 1) * 5;
    this.a = this.randomNumber(critLevel);
    this.b = this.randomNumber(critLevel);
    if (this.a < this.b) {
      const temp = this.a;
      this.a = this.b;
      this.b = temp;
    }
    this.abCal = this.a - this.b;
    if (this.a === 0 || this.b === 0 || this.abCal > critLevel || this.b * 3 < this.a || this.abCal * 2 < critLevel) {
      this.divi();
    }
  }

  chooseNumber(key: any) {
    if (key === '-') {
      if (this.result === '') {
        this.result += key;
      }
    } else if (key === '.') {
      if (this.decimal === false && this.result !== '') {
        this.result += key;
        this.decimal = true;
      }
    } else {
      this.result += key;
    }
  }

  clearResult() {
    this.result = '';
    this.decimal = false;
  }

  uplevel() {
    this.level++;
    if (this.maxLevel <= this.level) {
      this.maxLevel = this.level;
    }
  }

  checkResult() {
    if (Number(this.result) === this.abCal && this.abCal !== 0) {
      this.countDownTime += this.plusTime;
      this.clearResult();
      this.calculation();
      this.uplevel();
    } else {
      this.clearResult();
    }
  }

  randomNumber(size: any) {
    return Math.floor(Math.random() * size);
  }

  startCountdown() {
    this.interval = setInterval(() => {
      this.countDownTime--;
      if (this.countDownTime < 0) {
        clearInterval(this.interval);
        this.start = false;
        this.countDownTime = this.gameTime;
      }
    }, 1000);
  }
}
